body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #26394e;
}

#menu {
  height: 100%;
  position: fixed;
  z-index: 100;
  background: rgba(255, 255, 255, 0.8);
  width: 300px;
  transition: 1000ms all cubic-bezier(0.19, 1, 0.22, 1);
  transform: translateX(80%);
  right: 0px;
}
#menu:hover {
  transform: translateX(0%);
  background: rgba(255, 255, 255, 1);
}

.menu-inner {
  width: 100%;
  height: 100%;
  position: relative;
}

.hamburger {
  left: 20px;
  top: 50%;
  position: absolute;
  width: 20px;
  height: 20px;
  margin-top: -10px;
}

.hamburger .line {
  width: 100%;
  height: 4px;
  background-color: #222;
  position: absolute;
}
.hamburger:hover .line {
  background: red;
}

.hamburger .line:nth-child(2) {
  top: 50%;
  margin-top: -2px;
}

.hamburger .line:nth-child(3) {
  bottom: 0;
}

.menu-inner h1 {
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
}
h1 {
  width: 80px;
  margin: 0 auto;
}
h1 img {
  width: 100%;
  position: absolute;
  top: 20px;
}
h1 img:nth-child(1) {
  position: absolute;
  z-index: 2;
}
h1 img:nth-child(2) {
  position: absolute;
}
h1 img:nth-child(1):hover {
  display: none;
}
.menu-inner ul {
  padding: 0;
  list-style: none;
  text-align: center;
  width: 50%;
  position: absolute;
  top: 25%;

  left: 50%;
  transform: translateX(-50%);
}

.menu-inner ul li {
  padding: 20px 0;
}
.menu-inner ul li a {
  display: block;
  transition: all 0.5s;
}
.menu-inner ul li a:hover {
  color: red;
}

p.email {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.9em;
}

p.email a {
  transition: all 0.5s;
}
p.email:hover a {
  color: red;
}

