#blob {
  top: 0;
  z-index: -1;
  right: 240px;
  transform: translateX(100%);
  transform: rotateY(180deg);
  height: 100%;
  position: absolute;
}

#blob-path {
  height: 100%;
  fill: #fed057;
}

